.container-config{width:100%;max-width:900px}.container-config .title{font-weight:500;font-size:23px;line-height:27px;color:#081131}.container-input{border:1px dashed #999999;padding:15px 0 0}.container-input__setting h3{font-weight:500;font-size:18px;line-height:21px;color:#000000;margin-bottom:23px}.container-input__setting label{font-weight:400;font-size:16px;line-height:19px;color:#000000;margin-bottom:10px}.container-input__setting input{margin-bottom:50px;max-width:550px}.container-input-file{position:relative;width:100%;aspect-ratio:181/128;border:2px solid #000000;display:flex;align-items:center;justify-content:center;margin-bottom:1.5rem}.container-input-file:hover .icon-remove{opacity:1;visibility:visible}.container-input-file .input-file{position:absolute;top:0;left:0;right:0;bottom:0;opacity:0;cursor:pointer}.container-input-file .icon-remove{position:absolute;top:-13.5px;right:-13.5px;background:#A185E1;border-radius:50%;padding:5px;color:#fff;cursor:pointer;width:27px;height:27px;opacity:0;visibility:hidden;transition:.3s}.info-image{background:#FFFFFF;box-shadow:0 0 5px rgba(0,0,0,0.15);border-radius:8px;padding:20px;margin-top:26px}.system-config-contain{padding:7px 15px}.system-config-contain .card-header{font-weight:500;font-size:18px;line-height:21px;text-transform:uppercase;color:black}.system-config-contain .guide-content{font-style:italic;font-weight:400;font-size:13px;line-height:25px;color:#555555;margin-top:16px}.system-config-contain .hr-style{margin-top:33px;background-color:black;height:0.5px;margin-bottom:39px}
